@import "@ramsey-design-system/tokens/dist/variables";

$rds-heading-line-height: $rds-size-line-height-md;
$rds-heading-xlarge-and-up-line-height: $rds-size-line-height-sm;
$rds-heading-font-weight: $rds-font-weight-heading;

$rds-heading-text-color: $rds-color-base-gray-90;
$rds-heading-alternative-text-color: $rds-color-base-blue-80;
$rds-heading-alternative-interactive-text-color: $rds-color-base-blue-60;
$rds-heading-link-interactive-text-color: $rds-color-base-gray-70;
$rds-heading-brand-text-color: var(
  --rds-heading-brand-color,
  $rds-color-base-blue-50
);
$rds-heading-brand-interactive-text-color: var(
  --rds-heading-brand-interactive-color,
  $rds-color-base-blue-60
);
$rds-heading-text-color-on-dark-canvas: $rds-color-base-white;
$rds-heading-interactive-text-color-on-dark-canvas: $rds-color-base-gray-10;

$rds-heading-text-size: $rds-size-font-6;
$rds-heading-xsmall-text-size: $rds-size-font-4;
$rds-heading-small-text-size: $rds-size-font-5;
$rds-heading-large-mobile-text-size: $rds-size-font-7;
$rds-heading-large-text-size: $rds-size-font-8;
$rds-heading-xlarge-mobile-text-size: $rds-size-font-8;
$rds-heading-xlarge-text-size: $rds-size-font-10;
$rds-heading-2xlarge-mobile-text-size: $rds-size-font-9;
$rds-heading-2xlarge-text-size: $rds-size-font-11;
