@import "@ramsey-design-system/tokens/dist/mixins";
@import "variables";
@import "mixins";

.rds-Heading {
  @include rds-heading-base;

  margin-top: 0;
  margin-bottom: 0;
  text-indent: 0;
  text-transform: none;
  text-shadow: none;
  letter-spacing: 0;
  font-style: normal;

  &--xsmall {
    @include rds-heading-xsmall;
  }

  &--small {
    @include rds-heading-small;
  }

  &--large {
    @include rds-heading-large;
  }

  &--xlarge {
    @include rds-heading-xlarge;
  }

  &--2xlarge {
    @include rds-heading-2xlarge;
  }

  em {
    font-style: italic;
  }

  a {
    @include rds-heading-link;
  }

  &--alternate {
    @include rds-heading-alternate;
  }

  &--brand {
    @include rds-heading-brand;
  }

  &.on-darkCanvas {
    @include rds-heading-on-dark-canvas;
  }
}
