@import "@ramsey-design-system/tokens/dist/variables";
@import "@ramsey-design-system/tokens/dist/mixins";
@import "mixins";
@import "functions";

.rds-Button {
  // Font
  $rds-button-font-family: $rds-font-family-gibson;
  $rds-button-font-weight: $rds-font-weight-semibold;
  $rds-button-font-size: $rds-size-font-3;

  // Size
  $rds-button-border-width: $rds-size-border-sm;
  $rds-button-border-radius: $rds-size-radius-sm;
  $rds-button-padding: $rds-size-spacing-inset-squish-md;
  $rds-button-small-padding: $rds-size-spacing-inset-squish-sm;
  $rds-button-small-font-size: $rds-size-font-2;
  $rds-button-large-padding: $rds-size-spacing-inset-squish-lg;
  $rds-button-large-font-size: $rds-size-font-4;
  $rds-button-xlarge-padding: $rds-size-spacing-inset-squish-xl;
  $rds-button-xlarge-font-size: $rds-size-font-5;

  // Appearance
  $rds-button-text-color: $rds-color-base-white;
  $rds-button-background-color: $rds-color-base-blue-50;
  $rds-button-text-color-on-dark-canvas: $rds-button-background-color;
  $rds-button-background-color-interactive: $rds-color-base-blue-60;
  $rds-button-background-color-on-dark-canvas: $rds-color-base-white;
  $rds-button-background-color-interactive-on-dark-canvas: rgba(
    $rds-color-base-white,
    0.9
  );
  $rds-button-alternate-text-color: $rds-color-base-blue-80;
  $rds-button-alternate-background-color: $rds-color-base-yellow-50;
  $rds-button-alternate-background-color-interactive: $rds-color-base-yellow-30;
  $rds-button-ghost-border-color-on-dark-canvas: $rds-color-base-white;
  $rds-button-ghost-border-color: $rds-button-background-color;
  $rds-button-ghost-text-color: $rds-button-background-color;
  $rds-button-ghost-text-color-on-dark-canvas: $rds-color-base-white;
  $rds-button-transparent-background-color-interactive: rgba(
    $rds-color-base-blue-50,
    0.1
  );
  $rds-button-transparent-background-color-interactive-on-dark-canvas: rgba(
    $rds-color-base-white,
    0.1
  );
  $rds-button-subtle-text-color: $rds-button-background-color;
  $rds-button-subtle-text-color-on-dark-canvas: $rds-color-base-white;
  $rds-button-danger-text-color: $rds-color-base-white;
  $rds-button-danger-text-color-on-dark-canvas: $rds-color-base-red-50;
  $rds-button-danger-background-color: $rds-color-base-red-50;
  $rds-button-danger-background-color-on-dark-canvas: $rds-color-base-white;
  $rds-button-danger-background-color-interactive: $rds-color-base-red-60;
  $rds-button-subtle-neutral-text-color: $rds-color-base-gray-90;
  $rds-button-subtle-neutral-background-color-interactive: rgba(
    $rds-color-base-gray-20,
    0.44
  );
  $rds-button-subtle-neutral-text-color-on-dark-canvas: $rds-color-base-white;

  // Animation
  $rds-button-transition-duration: 0.3s;
  $rds-button-transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);

  // Icon
  $rds-button-icon-spacing: $rds-size-spacing-inline-sm;
  $rds-button-icon-only-padding: $rds-size-spacing-inset-sm;
  $rds-button-icon-only-small-padding: $rds-size-spacing-inset-xs;
  $rds-button-icon-only-large-padding: calc-icon-only-padding(52px);
  $rds-button-icon-only-xlarge-padding: calc-icon-only-padding(62px);

  box-sizing: border-box;
  display: inline-flex;
  align-items: center;
  flex-shrink: 0;
  justify-content: center;
  transition: background-color $rds-button-transition-duration
      $rds-button-transition-timing-function,
    border-color $rds-button-transition-duration
      $rds-button-transition-timing-function,
    color $rds-button-transition-duration $rds-button-transition-timing-function;
  border: $rds-button-border-width solid transparent;
  border-radius: $rds-button-border-radius;
  box-shadow: $rds-button-background-color inset;
  background-color: var(
    --rds-button-background-color,
    $rds-button-background-color
  );
  cursor: pointer;
  padding: $rds-button-padding;
  width: auto;
  text-align: center;
  text-indent: 0;
  text-transform: none;
  text-decoration: none;
  text-shadow: none;
  line-height: $rds-size-line-height-reset;
  letter-spacing: normal;
  color: var(--rds-button-text-color, $rds-button-text-color);
  font-family: $rds-button-font-family;
  font-size: $rds-button-font-size;
  font-weight: $rds-button-font-weight;
  font-style: normal;
  word-break: break-word;

  @include rds-button-interactive-state {
    background-color: var(
      --rds-button-background-color-interactive,
      $rds-button-background-color-interactive
    );
    color: $rds-button-text-color;
  }

  &.on-darkCanvas {
    background-color: $rds-button-background-color-on-dark-canvas;
    color: var(
      --rds-button-background-color,
      $rds-button-text-color-on-dark-canvas
    );

    @include rds-button-interactive-state {
      background-color: $rds-button-background-color-interactive-on-dark-canvas;
      color: var(
        --rds-button-background-color,
        $rds-button-text-color-on-dark-canvas
      );
    }
  }

  &--alternate,
  &--alternate.on-darkCanvas {
    background-color: var(
      --rds-button-alternate-background-color,
      $rds-button-alternate-background-color
    );
    color: var(
      --rds-button-alternate-text-color,
      $rds-button-alternate-text-color
    );

    @include rds-button-interactive-state {
      background-color: var(
        --rds-button-alternate-background-color-interactive,
        $rds-button-alternate-background-color-interactive
      );
      color: var(
        --rds-button-alternate-text-color,
        $rds-button-alternate-text-color
      );
    }
  }

  &--ghost {
    border-color: var(
      --rds-button-background-color,
      $rds-button-ghost-border-color
    );
    box-shadow: 0 0 0 1px $rds-button-ghost-border-color inset;
    background-color: transparent;
    color: var(--rds-button-background-color, $rds-button-ghost-text-color);

    &.on-darkCanvas {
      border-color: $rds-button-ghost-border-color-on-dark-canvas;
      box-shadow: 0 0 0 1px $rds-button-ghost-border-color-on-dark-canvas inset;
      background-color: transparent;
      color: $rds-button-ghost-text-color-on-dark-canvas;

      @include rds-button-interactive-state {
        background-color: $rds-button-transparent-background-color-interactive-on-dark-canvas;
        color: $rds-button-ghost-text-color-on-dark-canvas;
      }
    }

    @include rds-button-interactive-state {
      background-color: var(
        --rds-button-transparent-background-color-interactive,
        $rds-button-transparent-background-color-interactive
      );
      color: var(--rds-button-background-color, $rds-button-ghost-text-color);
    }
  }

  &--subtle {
    background-color: transparent;
    color: var(--rds-button-background-color, $rds-button-subtle-text-color);

    &.on-darkCanvas {
      background-color: transparent;
      color: $rds-button-subtle-text-color-on-dark-canvas;

      @include rds-button-interactive-state {
        background-color: $rds-button-transparent-background-color-interactive-on-dark-canvas;
        color: $rds-button-subtle-text-color-on-dark-canvas;
      }
    }

    @include rds-button-interactive-state {
      background-color: var(
        --rds-button-transparent-background-color-interactive,
        $rds-button-transparent-background-color-interactive
      );
      color: var(--rds-button-background-color, $rds-button-subtle-text-color);
    }
  }

  &--danger {
    background-color: $rds-button-danger-background-color;
    color: $rds-button-danger-text-color;

    &.on-darkCanvas {
      background-color: $rds-button-danger-background-color-on-dark-canvas;
      color: $rds-button-danger-text-color-on-dark-canvas;

      @include rds-button-interactive-state {
        background-color: $rds-button-background-color-interactive-on-dark-canvas;
        color: $rds-button-danger-text-color-on-dark-canvas;
      }
    }

    @include rds-button-interactive-state {
      background-color: $rds-button-danger-background-color-interactive;
      color: $rds-button-danger-text-color;
    }
  }

  &--dangerSubtle {
    background-color: transparent;
    color: $rds-button-danger-background-color;

    &.on-darkCanvas {
      background-color: $rds-button-danger-background-color-on-dark-canvas;
      color: $rds-button-danger-text-color-on-dark-canvas;

      @include rds-button-interactive-state {
        background-color: $rds-button-background-color-interactive-on-dark-canvas;
        color: $rds-button-danger-text-color-on-dark-canvas;
      }
    }

    @include rds-button-interactive-state {
      background: rgba($rds-button-danger-background-color, 0.1);
      color: $rds-button-danger-text-color-on-dark-canvas;
    }
  }

  &--neutralSubtle {
    background-color: transparent;
    color: $rds-button-subtle-neutral-text-color;

    &.on-darkCanvas {
      background-color: transparent;
      color: $rds-button-subtle-neutral-text-color-on-dark-canvas;

      @include rds-button-interactive-state {
        background-color: $rds-button-transparent-background-color-interactive-on-dark-canvas;
        color: $rds-button-subtle-neutral-text-color-on-dark-canvas;
      }
    }

    @include rds-button-interactive-state {
      background-color: $rds-button-subtle-neutral-background-color-interactive;
      color: $rds-button-subtle-neutral-text-color;
    }
  }

  &--small {
    padding: $rds-button-small-padding;
    font-size: $rds-button-small-font-size;
  }

  &--large {
    padding: $rds-button-large-padding;
    font-size: $rds-button-large-font-size;
  }

  &--xlarge {
    padding: $rds-button-xlarge-padding;
    font-size: $rds-button-xlarge-font-size;
  }

  &--fullWidth {
    width: 100%;
  }

  &--fullWidthOnMobile {
    width: 100%;

    @include rds-media-query-sm-and-up {
      width: auto;
    }
  }

  &--iconOnly {
    padding: $rds-button-icon-only-padding;
  }

  &--iconOnly#{&}--small {
    padding: $rds-button-icon-only-small-padding;
  }

  &--iconOnly#{&}--large {
    padding: $rds-button-icon-only-large-padding;
  }

  &--iconOnly#{&}--xlarge {
    padding: $rds-button-icon-only-xlarge-padding;
  }

  &-icon {
    flex-shrink: 0;
    pointer-events: none;

    &--left {
      margin-right: $rds-button-icon-spacing;
    }

    &--right {
      margin-left: $rds-button-icon-spacing;
    }
  }

  &[disabled] {
    cursor: not-allowed !important;
  }

  &[disabled],
  &.is-disabled {
    opacity: 0.4 !important;
    user-select: none !important;
  }

  @at-root a#{&} {
    &.is-disabled {
      pointer-events: none !important;
    }
  }
}
