.rds-Heading {
  line-height: 1.25;
  color: #1f2426;
  font-family: canada-type-gibson, Arial, sans-serif;
  font-size: 24px;
  font-weight: 600;
  margin-top: 0;
  margin-bottom: 0;
  text-indent: 0;
  text-transform: none;
  text-shadow: none;
  letter-spacing: 0;
  font-style: normal;
}
.rds-Heading--xsmall {
  font-size: 18px;
}
.rds-Heading--small {
  font-size: 20px;
}
.rds-Heading--large {
  font-size: 28px;
}
@media screen and (min-width: 37.5em) {
  .rds-Heading--large {
    font-size: 34px;
  }
}
.rds-Heading--xlarge {
  font-size: 34px;
}
@media screen and (min-width: 37.5em) {
  .rds-Heading--xlarge {
    line-height: 1.1;
    font-size: 48px;
  }
}
.rds-Heading--2xlarge {
  font-size: 40px;
}
@media screen and (min-width: 37.5em) {
  .rds-Heading--2xlarge {
    line-height: 1.1;
    font-size: 60px;
  }
}
.rds-Heading em {
  font-style: italic;
}
.rds-Heading a {
  text-decoration: none;
  color: inherit;
}
.rds-Heading a:hover {
  color: #495257;
}
.rds-Heading--alternate {
  color: #003561;
}
.rds-Heading--alternate a:hover {
  color: #005b98;
}
.rds-Heading--brand {
  color: var(--rds-heading-brand-color, #0073b9);
}
.rds-Heading--brand a:hover {
  color: var(--rds-heading-brand-interactive-color, #005b98);
}
.rds-Heading.on-darkCanvas {
  color: #ffffff;
}
.rds-Heading.on-darkCanvas a:hover {
  color: #f5f7f8;
}

