@import "@ramsey-design-system/tokens/dist/mixins";
@import "variables";

@mixin rds-heading-base {
  line-height: $rds-heading-line-height;
  color: $rds-heading-text-color;
  font-family: $rds-font-family-gibson;
  font-size: $rds-heading-text-size;
  font-weight: $rds-heading-font-weight;
}

@mixin rds-heading-xsmall {
  font-size: $rds-heading-xsmall-text-size;
}

@mixin rds-heading-small {
  font-size: $rds-heading-small-text-size;
}

@mixin rds-heading-large {
  font-size: $rds-heading-large-mobile-text-size;

  @include rds-media-query-sm-and-up {
    font-size: $rds-heading-large-text-size;
  }
}

@mixin rds-heading-xlarge {
  font-size: $rds-heading-xlarge-mobile-text-size;

  @include rds-media-query-sm-and-up {
    line-height: $rds-heading-xlarge-and-up-line-height;
    font-size: $rds-heading-xlarge-text-size;
  }
}

@mixin rds-heading-2xlarge {
  font-size: $rds-heading-2xlarge-mobile-text-size;

  @include rds-media-query-sm-and-up {
    line-height: $rds-heading-xlarge-and-up-line-height;
    font-size: $rds-heading-2xlarge-text-size;
  }
}

@mixin rds-heading-link {
  text-decoration: none;
  color: inherit;

  &:hover {
    color: $rds-heading-link-interactive-text-color;
  }
}

@mixin rds-heading-alternate {
  color: $rds-heading-alternative-text-color;

  a:hover {
    color: $rds-heading-alternative-interactive-text-color;
  }
}

@mixin rds-heading-brand {
  color: $rds-heading-brand-text-color;

  a:hover {
    color: $rds-heading-brand-interactive-text-color;
  }
}

@mixin rds-heading-on-dark-canvas {
  color: $rds-heading-text-color-on-dark-canvas;

  a:hover {
    color: $rds-heading-interactive-text-color-on-dark-canvas;
  }
}
