@import "@ramsey-design-system/tokens/dist/variables";

.rds-Stack {
  $this: &;

  // Spacing
  $rds-stack-spacing-small: $rds-size-spacing-inline-sm;
  $rds-stack-spacing-medium: $rds-size-spacing-inline-md;
  $rds-stack-spacing-large: $rds-size-spacing-inline-xl;

  // Size
  $_sub-pixel-rounding-offset: 1px;

  $rds-stack-item-max-width-small: calc(
    100% - #{$rds-stack-spacing-small - $_sub-pixel-rounding-offset}
  );
  $rds-stack-item-max-width-medium: calc(
    100% - #{$rds-stack-spacing-medium - $_sub-pixel-rounding-offset}
  );
  $rds-stack-item-max-width-large: calc(
    100% - #{$rds-stack-spacing-large - $_sub-pixel-rounding-offset}
  );

  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin-top: -$rds-stack-spacing-medium !important;
  margin-left: -$rds-stack-spacing-medium !important;

  > * {
    margin-top: $rds-stack-spacing-medium !important;
    margin-left: $rds-stack-spacing-medium !important;
    max-width: $rds-stack-item-max-width-medium;
  }

  &--smallSpacing {
    margin-top: -$rds-stack-spacing-small !important;
    margin-left: -$rds-stack-spacing-small !important;

    > * {
      margin-top: $rds-stack-spacing-small !important;
      margin-left: $rds-stack-spacing-small !important;
      max-width: $rds-stack-item-max-width-small;
    }
  }

  &--largeSpacing {
    margin-top: -$rds-stack-spacing-large !important;
    margin-left: -$rds-stack-spacing-large !important;

    > * {
      margin-top: $rds-stack-spacing-large !important;
      margin-left: $rds-stack-spacing-large !important;
      max-width: $rds-stack-item-max-width-large;
    }
  }

  &--topVerticalAlignment {
    align-items: flex-start;
  }

  &--bottomVerticalAlignment {
    align-items: flex-end;
  }

  &--centerHorizontalAlignment {
    justify-content: center;
  }

  &--rightHorizontalAlignment {
    justify-content: flex-end;
  }

  &--vertical {
    align-items: flex-start;
    flex-direction: column;
    justify-content: center;
    margin-left: $rds-size-spacing-none;

    > * {
      margin-left: $rds-size-spacing-none;
      max-width: 100%;
    }

    &#{$this}--topVerticalAlignment {
      justify-content: flex-start;
    }

    &#{$this}--bottomVerticalAlignment {
      justify-content: flex-end;
    }

    &#{$this}--centerHorizontalAlignment {
      align-items: center;
    }

    &#{$this}--rightHorizontalAlignment {
      align-items: flex-end;
    }
  }

  &--noWrap {
    flex-wrap: nowrap;
  }
}
